.md2-colorpicker-wrapper {
  width: 270px;
  height: 345px;
  border-radius: 2px;
  background-color: #fff;
  z-index: 10;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.4);
  transform: scale(0);
  transform-origin: left top;
  transition: 150ms;
  user-select: none; }
  .md2-colorpicker-wrapper.active {
    transform: scale(1); }

.md2-colorpicker-disabled {
  pointer-events: none;
  cursor: default; }
  .md2-colorpicker-disabled .color-picker-selector .color-text {
    color: rgba(0, 0, 0, 0.38);
    border-color: transparent;
    background-image: linear-gradient(to right, rgba(0, 0, 0, 0.38) 0%, rgba(0, 0, 0, 0.38) 33%, transparent 0%);
    background-position: bottom -1px left 0;
    background-size: 4px 1px;
    background-repeat: repeat-x; }

.color-picker-selector {
  display: block;
  padding: 18px 0 18px 32px;
  white-space: nowrap;
  cursor: pointer; }
  .color-picker-selector .color-div {
    content: '';
    width: 24px;
    height: 24px;
    overflow: hidden;
    background-color: #fff;
    background-image: -webkit-linear-gradient(45deg, #ddd 25%, transparent 0, transparent 75%, #ddd 0, #ddd), -webkit-linear-gradient(45deg, #ddd 25%, transparent 0, transparent 75%, #ddd 0, #ddd);
    background-image: linear-gradient(45deg, #ddd 25%, transparent 0, transparent 75%, #ddd 0, #ddd), linear-gradient(45deg, #ddd 25%, transparent 0, transparent 75%, #ddd 0, #ddd);
    background-size: 8px 8px;
    background-position: 0 0, 4px 4px;
    position: absolute;
    top: 21px;
    left: 0;
    border: 2px solid #fafafa;
    display: block;
    fill: currentColor;
    cursor: pointer;
    border-radius: 50%;
    vertical-align: middle;
    box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.2), 0 1px 1px 1px rgba(0, 0, 0, 0.14), 0 1px 1px 1px rgba(0, 0, 0, 0.12); }
    .color-picker-selector .color-div .color-fill {
      width: 100%;
      height: 100%; }
  .color-picker-selector .color-text {
    cursor: pointer;
    position: relative;
    display: block;
    min-width: 150px;
    height: 30px;
    padding: 2px 26px 1px 2px;
    margin: 0;
    line-height: 26px;
    color: rgba(0, 0, 0, 0.87);
    vertical-align: middle;
    box-sizing: border-box;
    border-bottom: 1px solid rgba(0, 0, 0, 0.12); }

md2-colorpicker {
  position: relative;
  display: block;
  max-width: 175px;
  outline: none;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden; }

.md2-color-picker {
  position: relative;
  display: block;
  outline: none;
  backface-visibility: hidden; }
  .md2-color-picker * {
    box-sizing: border-box; }
  .md2-color-picker .input-color-content {
    width: 250px;
    position: relative;
    margin: 10px auto; }
  .md2-color-picker i {
    cursor: default;
    position: relative; }
  .md2-color-picker input {
    font-size: 15px; }
  .md2-color-picker div.cursor-sv {
    cursor: default;
    position: relative;
    border-radius: 50%;
    width: 15px;
    height: 15px;
    border: #ddd solid 1px; }
  .md2-color-picker div.cursor {
    cursor: crosshair;
    position: relative;
    border-radius: 50%;
    width: 13px;
    height: 13px;
    box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.5), inset 0 0 2px 0 rgba(0, 0, 0, 0.5);
    border: 2px solid #fff; }
  .md2-color-picker div.color-picker-marker {
    cursor: crosshair;
    position: relative;
    border: 2px solid #fff;
    box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.5);
    height: 100%;
    width: 5px;
    border-bottom: 0;
    border-top: 0; }
  .md2-color-picker .saturation-lightness {
    width: 100%;
    height: 130px;
    border: none;
    overflow: hidden;
    background-image: linear-gradient(to top, #000, transparent), linear-gradient(to right, #fff, rgba(255, 255, 255, 0));
    -ms-filter: 'progid:DXImageTransform.Microsoft.gradient(startColorstr=#00CC9A81, endColorstr=#FF000000)';
    filter: progid:dximagetransform.microsoft.gradient(startColorstr='#00CC9A81', endColorstr='#FF000000'); }
  .md2-color-picker .saturation-lightness:hover {
    cursor: crosshair; }
  .md2-color-picker .hue {
    width: 100%;
    height: 30px;
    border: none;
    margin: 10px 0;
    background: -webkit-linear-gradient(left, #f00 0%, #ff0 16.66%, #0f0 33.33%, #0ff 50%, #00f 66.66%, #f0f 83.33%, #f00 100%); }
  .md2-color-picker .alpha {
    border: 1px solid #efefef;
    width: 100%;
    height: 30px;
    background-image: linear-gradient(to left, transparent, transparent), linear-gradient(to right, #fff, rgba(255, 255, 255, 0)); }
  .md2-color-picker .selected-color {
    width: 100%;
    height: 65px;
    padding-top: 10px;
    position: relative; }

.hex-text {
  width: 100%; }
  .hex-text input {
    width: 100%;
    border: 0;
    padding: 4px;
    text-align: center;
    background: transparent;
    outline: none;
    font-size: 15px; }
  .hex-text div {
    text-align: center;
    float: left;
    clear: left;
    width: 160px;
    margin-top: 4px; }

.hsla-text,
.rgba-text {
  text-align: center; }

.hsla-text input,
.rgba-text input {
  width: 50px;
  border: 0;
  padding: 4px 0;
  background: transparent;
  text-align: center; }

.hsla-text div,
.rgba-text div {
  text-align: center;
  display: block; }

.hsla-text label,
.rgba-text label {
  text-align: center;
  display: inline-block;
  font-size: 15px; }

.md2-color-picker-actions {
  text-align: right; }
  .md2-color-picker-actions .md2-button {
    display: inline-block;
    min-width: 64px;
    margin: 4px 8px 8px 0;
    padding: 0 12px;
    font-size: 14px;
    color: #106cc8;
    line-height: 36px;
    text-align: center;
    text-transform: uppercase;
    border-radius: 2px;
    cursor: pointer;
    box-sizing: border-box;
    transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1); }
    .md2-color-picker-actions .md2-button:hover {
      background: #ebebeb; }

.hsla-text div:nth-child(5),
.rgba-text div:nth-child(5) {
  clear: left; }

.type-policy {
  width: 33.3%;
  text-align: center;
  font-size: 14px;
  display: inline-block;
  float: left;
  padding: 2px;
  margin-top: 6px;
  cursor: pointer;
  background: rgba(255, 255, 255, 0.4); }

.type-policy.active {
  background: rgba(153, 153, 153, 0.2) !important; }

.cdk-overlay-container, .cdk-global-overlay-wrapper {
  pointer-events: none;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%; }

.cdk-overlay-container {
  position: fixed;
  z-index: 1000; }

.cdk-global-overlay-wrapper {
  display: flex;
  position: absolute;
  z-index: 1000; }

.cdk-overlay-pane {
  position: absolute;
  pointer-events: auto;
  box-sizing: border-box;
  z-index: 1000; }

.cdk-overlay-backdrop {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  pointer-events: auto;
  transition: opacity 400ms cubic-bezier(0.25, 0.8, 0.25, 1);
  opacity: 0; }

.cdk-overlay-backdrop.cdk-overlay-backdrop-showing {
  opacity: 0.48; }

.cdk-overlay-dark-backdrop {
  background: rgba(0, 0, 0, 0.6); }
